import kotlinx.browser.document
import kotlinx.browser.window
import kotlinx.html.*
import kotlinx.html.dom.append
import org.w3c.dom.Node
import org.w3c.dom.events.Event

fun main() {
    document.body?.renderContent()
    window.onload = ::onLoad
}

@Suppress("UNUSED_PARAMETER")
private fun onLoad(event: Event) {
    document.body?.className = document.body?.className?.replace("is-preload", "").orEmpty()
    document.body?.className += if (window.navigator.userAgent.matches("(MSIE|rv:11\\.0)".toRegex())) " is-ie" else ""
}

private fun Node.renderContent() = append {
    div {
        id = "wrapper"
        aboutMeSection()
        websiteFooter()
    }
}

private fun HtmlBlockTag.aboutMeSection() = section {
    id = "main"
    cardHeader()
    cardFooter()
    projectsSection()
}

private fun HtmlBlockTag.cardHeader() = header {
    span(classes = "avatar") { img(src = "images/magnum.jpg", alt = "") }
    h1 { +"Magnum Rocha" }
    p { +"Mobile Developer" }
}

private fun HtmlBlockTag.cardFooter() = footer {
    ul(classes = "icons") {
        mapOf(
            "github" to "https://github.com/magnumrocha",
            "medium" to "https://medium.com/@magnumrocha",
            "linkedin" to "https://www.linkedin.com/in/magnumrocha"
        ).forEach { (media, link) ->
            li {
                a(href = link, classes = "icon brands fa-$media") {
                    +media
                }
            }
            +" "
        }
    }
}

private fun HtmlBlockTag.projectsSection() = section {
    id = "projects"
    hr()
    h2 { +"Check out my Work" }
    ul {
        style = "list-style-type: none;"
        li {
            a(href = "https://github.com/TM-Apps/konnection") {
                u { +"Konnection" }
            }
        }
    }
    hr()
}

private fun HtmlBlockTag.extraContent() {
    hr()
    h2 { +"Extra Stuff!" }
    form(method = FormMethod.post, action = "#") {
        div(classes = "fields") {
            div(classes = "field") {
                input(type = InputType.text, name = "name") {
                    id = "name"
                    placeholder = "Name"
                }
            }
            div(classes = "field") {
                input(type = InputType.email, name = "email") {
                    id = "email"
                    placeholder = "Email"
                }
            }
            div(classes = "field") {
                select {
                    name = "department"
                    id = "department"
                    
                    option { value = ""; +"Department" }
                    option { value = "sales"; +"Sales" }
                    option { value = "tech"; +"Tech Support" }
                    option { value = "null"; +"/dev/null" }
                }
            }
            div(classes = "field") {
                textArea(rows = "4") {
                    name = "message"
                    id = "message"
                }
            }
            div(classes = "field") {
                input(type = InputType.checkBox, name = "human") {
                    id = "human"
                }
                label { htmlFor="human"; +"I'm a human" }
            }
            div(classes = "field") {
                label { +"But are you a robot?" }
                input(type = InputType.radio, name = "robot") {
                    id = "robot_yes"
                }
                label { htmlFor="robot_yes"; +"Yes" }
                input(type = InputType.radio, name = "robot") {
                    id = "robot_no"
                }
                label { htmlFor="robot_no"; +"No" }
            }
        }
        ul(classes = "actions special") {
            li {
                a(href = "#", classes = "button") { +"Get Started" }
            }
        }
    }
    hr()
}

private fun HtmlBlockTag.websiteFooter() = footer {
    id = "footer"
    ul(classes = "copyright") {
        li { +" Magnum Rocha" }
        li {
            +"Design:"
            a(href = "http://html5up.net") {
                +" HTML5 UP"
            }
        }
    }
}
